import React,{useEffect} from 'react';
import store from 'src/store';
import {updateTesting} from "../../actions/testingActions";
//import {logoutUser} from "../../actions/permissionsActions";

const user = {
    "blarg":"dfgdfgfggf"
}

const TestingReducerTest = () => {
    useEffect(()=>{
        store.dispatch(updateTesting({
            user:user
        }))
        store.dispatch(updateTesting(user))
    },[])

//<button onClick={()=>store.dispatch(logoutUser())} id="logoutuser">Logout user</button>
    return (
        <div id="testredtest">
            <div>Logout user?

            </div>
        </div>
    )
};

export default TestingReducerTest;
